
//
// Load core variables and mixins
// --------------------------------------------------

@import "variables.less";
@import "mixins.less";

//
// responsive fonts
// --------------------------------------------------

@media (min-width: (@screen-sm + 1)) {
    #slider h1 {
        font-size: floor((@font-size-base * 4));
        -webkit-transform: translateZ(0);
        -moz-transform: translateZ(0);
        -ms-transform: translateZ(0);
        -o-transform: translateZ(0);
        transform: translateZ(0); // font smooth bugfix
    }
}


@media (max-width: @screen-xs) {
    body {
        font-size: 13px;
    }

    #slider h1 {
        font-size: floor((@font-size-base * 2.3));
    }

    h1, .h1 {
        font-size: floor((@font-size-h1 * 0.8));
    }

    h2, .h2 {
        font-size: floor((@font-size-h2 * 0.8));
    }

    h3, .h3 {
        font-size: floor((@font-size-h3 * 0.8));
    }

    h4, .h4 {
        font-size: floor((@font-size-h4 * 0.8));
    }

    h5, .h5 {
        font-size: floor((@font-size-h5 * 0.8));
    }

    h6, .h6 {
        font-size: floor((@font-size-h6 * 0.8));
    }
}

:focus {
    outline: 0 !important;
}


//
// Theme
// --------------------------------------------------

.body-bg-image {
    background-attachment: fixed;
    background-position: center center;
    background-size: cover;
    background-image: url(../img/background.jpg);
    background-color: #525252;
    background-repeat: no-repeat;
}


#slider {
    background-color: rgba(0, 0, 0, 0.54);
    min-height: 300px;
    color: #F3F3F3;

    .slider-content {
        padding: 60px 0;
    }

    .carousel, .carousel-inner {
        height: 100%;
    }

    .item {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        color: #fff;
        text-align: center;
        overflow: hidden;
    }

    .item div {
        background-color: rgba(0, 0, 0, 0.08);
    }
    // carousel special
    // Carousel Fading slide
    .carousel-fade .item {
        -webkit-transition: opacity 0.5s ease-in-out;
        -moz-transition: opacity 0.5s ease-in-out;
        -ms-transition: opacity 0.5s ease-in-out;
        -o-transition: opacity 0.5s ease-in-out;
        transition: opacity 0.5s ease-in-out;
    }

    .carousel-fade .active.left,
    .carousel-fade .active.right {
        left: 0;
        z-index: 2;
        opacity: 0;
        filter: alpha(opacity=0);
    }

    .carousel-fade .carousel-control {
        z-index: 3;
    }

    .carousel-fade .next,
    .carousel-fade .prev {
        left: 0;
        z-index: 1;
    }
    // Carousel Control
    .carousel-control {
        font-size: 48px;
    }
    // Animation
    .control-box, a.carousel-control, .carousel-indicators li {
        -webkit-transition: all 250ms ease;
        -moz-transition: all 250ms ease;
        -ms-transition: all 250ms ease;
        -o-transition: all 250ms ease;
        transition: all 250ms ease;
    }
    // Removing BS background
    .carousel .control-box {
        opacity: 0;
    }

    a.carousel-control.left {
        left: 0;
        background: none;
        border: 0;
    }

    a.carousel-control.right {
        right: 0;
        background: none;
        border: 0;
    }
    // Hover animation
    .carousel:hover .control-box {
        opacity: 1;
    }

    .carousel:hover a.carousel-control.left {
        left: 15px;
    }

    .carousel:hover a.carousel-control.right {
        right: 15px;
    }
    // Carouse Indicator
    .carousel-indicators li.active,
    .carousel-indicators li {
        border: 0;
    }

    .carousel-indicators li {
        background: #666;
        margin: 0 3px;
        width: 12px;
        height: 12px;
        border-radius: 0;
    }

    .carousel-indicators li.active {
        background: #fff;
        margin: 0 3px;
    }

    .carousel-caption {
        margin: auto auto;
    }
}

#about {
    background-color: rgba(248, 248, 248, 0.9);
    min-height: 300px;
    overflow-x: hidden; // animation fix
    overflow-y: hidden; // animation fix
    .about-content {
        padding-top: 60px;
