.swipebox {
    padding: 0;
}

.ratio-16-9 {
    position: relative;
    width: 100%; /* desired width */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    overflow: hidden;
}

.ratio-16-9:before {
    content: "";
    display: block;
    padding-top: 56.7%; /* initial ratio of 2:1*/
}

.ratio-16-9-content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

