/***** Sadie *****/
/*---------------*/

figure.effect figcaption::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 102%;
    height: 100%;
    background: -webkit-linear-gradient(top, rgba(72,76,97,0) 0%, rgba(0, 0, 0, 0.5) 75%);
    background: linear-gradient(to bottom, rgba(72,76,97,0) 0%, rgba(0, 0, 0, 0.5) 75%);
    content: '';
    opacity: 0;
    -webkit-transform: translate3d(0,50%,0);
    transform: translate3d(0,50%,0);
}

figure.effect h2 {
    position: absolute;
    top: 55%;
    left: 0;
    width: 100%;
    color: #fff;
    -webkit-transition: -webkit-transform 0.35s,opacity 0.35s, color 0.35s;
    transition: transform 0.35s,opacity 0.35s, color 0.35s;
    -webkit-transform: translate3d(0,-50%,0);
    transform: translate3d(0,-50%,0);
    text-align: center;
    opacity: 0;
}

figure.effect figcaption::before,
figure.effect p {
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
}

figure.effect p {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 2em;
    width: 100%;
    opacity: 0;
    -webkit-transform: translate3d(0,10px,0);
    transform: translate3d(0,10px,0);
    text-align: center;
    color: #fff;
}

figure.effect:hover h2 {
    opacity: 1;
    color: #fff;
    -webkit-transform: translate3d(0,-50%,0) translate3d(0,-40px,0);
    transform: translate3d(0,-50%,0) translate3d(0,-40px,0);
}

figure.effect:hover figcaption::before,
figure.effect:hover p {
    opacity: 1;
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
}


