//
// Style support
// --------------------------------------------------

// full width table parent for centering
.vcenterparent {
    height: 100%;
    width: 100%;
    display: table;
    padding: 0;
}

// full height row with vertical centered in table parent
.vcenter {
    height: 100%;
    display: table-cell;
    vertical-align: middle;
}

// div horizontal centered (need width)
.hcenter {
    float: none;
    margin: 0 auto;
}

.img-responsive-fill {
    width: 100%;
    height: auto;
}

// background transition mixin
.backtransition(@millisecond) {
    -webkit-transition: background-color @millisecond linear;
    -moz-transition: background-color @millisecond linear;
    -o-transition: background-color @millisecond linear;
    -ms-transition: background-color @millisecond linear;
    transition: background-color @millisecond linear;
}

.alert {
    margin-top: 15px;
    margin-bottom: -15px;
}

.section-header {
    margin-bottom: 22px;
    text-align: center;
}

//
// Plugins
// --------------------------------------------------
#swipebox-action {
    background-image: none;
    background-color: #515151;
    border: none;
}

#swipebox-caption {
    background-image: none;
    background-color: #515151;
    font-size: 20px;
    font-family: @font-family-sans-serif;
    text-shadow: none;
    padding-top: 4px;
    border: none;
